export default {
	data() {
		return {
			form: {
				collect: [{
					time: '2019-04-06 11:37',
					shopName: '麦田圈官网旗舰店',
					state: 5,

					goodsList: [{
						title: '香辣牛肉干',
						price: 88.88,
						image: '/static/goods.png',
						number: 1,
						attr: '规格 10*200g'
					}]
				}],
				goodsObj: {
					remarks: '香辣味的',
					allPay: '120',
					jifen: '100',
					preferential: '20.00',
					total: '880'
				},
				orderMsg:{
					number:'13245679851354664',
					num:'3212313213213213132',
					creatTime:'2019-11-05 11:12:12',
					payTime:'2019-11-05 11:12:12',
					payWay:'微信支付'
				}
			},
			img:[
				'../../static/order/fahuo.png',
				'../../static/order/fukuan.png',
				'../../static/order/shouhuo.png',
				'../../static/order/chenggong.png',
				'../../static/order/guanbi.png',
				'../../static/order/wancheng.png',
			],
			order:{
				status:'',
				img:'',
				wuliu:'',
				pay:'',
				statusMsg:'',
				color:''
			},
			statuss:'',
			show:false,
			marginBottom:'',
			steps: [
			      {
			        text: '步骤一',
			        desc: '描述信息'
			      },
			      {
			        text: '步骤二',
			        desc: '描述信息'
			      },
			      {
			        text: '步骤三',
			        desc: '描述信息'
			      },
			      {
			        text: '步骤四',
			        desc: '描述信息'
			      }
			    ]
		}
	},
	onLoad(option){
		console.log(option)
		this.statuss = option.status;
		if(this.statuss==1){
			// 待支付
			this.show = true;
			this.order = {
				status:1,
				statusMsg:'',
				img:this.img[1],
				wuliu:'取消订单',
				pay:'立即支付'
			}
		}else if(this.statuss==4){
			//待发货
			this.show = false;
			this.order = {
				status:4,
				statusMsg:'待发货',
				img:this.img[0],
				wuliu:'',
				pay:'',
				color:'#F7B62C'
			}
		}else if(this.statuss==2){
			//待收货
			this.show = true;
			this.order = {
				status:2,
				statusMsg:'待收货',
				img:this.img[2],
				wuliu:'查看物流',
				pay:'确认收货',
				color:'#515151'
			}
		}else if(this.statuss==6){
			//已完成
			this.show = true;
			this.order = {
				status:2,
				statusMsg:'交易成功',
				img:this.img[3],
				wuliu:'删除订单',
				pay:'去评价',
				color:'#01B300'
			}
		}else if(this.statuss==9){
			// 交易关闭
			this.show = true;
			this.order = {
				status:9,
				statusMsg:'',
				img:this.img[4],
				wuliu:'删除订单',
				pay:'',
				color:''
			}
		}else if(this.statuss==7){
			// 已完成
			this.show = true;
			this.order = {
				status:7,
				statusMsg:'已完成',
				img:this.img[5],
				wuliu:'删除订单',
				pay:'再次购买',
				color:'#515151'
			}
		}
		
		
	},
	methods: {
		consult(){
			uni.navigateTo({
				url:'consult'
			})
		},
		// 查看物流
		logisticsTap(){
			uni.navigateTo({
				url:'logistics'
			})
		}
	}
}
